<template>
    <div class="option-node">
        <Icon class="icon" type="ios-send-outline" :size="24"/>
        <BetterInput class="text" :init-value="node.text" @confirm="handleConfirm"/>
        <Icon class="icon" type="ios-arrow-forward"/>
        <Dropdown class="target" trigger="click" @on-click="selectTarget">
            <Button class="target-button">
                {{ targetText }}
                <Icon type="ios-arrow-down"/>
            </Button>
            <DropdownMenu slot="list">
                <DropdownItem name="">----</DropdownItem>
                <DropdownItem v-for="label of labels" :key="label.uid" :name="label.uid">{{ label.text }}</DropdownItem>
            </DropdownMenu>
        </Dropdown>
    </div>
</template>

<script>
    import {Icon, Dropdown, DropdownMenu, DropdownItem, Button} from 'view-design';
    import BetterInput from "@/components/BetterInput";
    import {mapGetters} from 'vuex';
    export default {
        name: "OptionNode",
        components: {BetterInput, Icon, Dropdown, DropdownMenu, DropdownItem, Button},
        props: {
            node: {
                type: Object,
                required: true,
            },
        },
        data() {
            return {
                targetText: '----',
            };
        },
        computed: mapGetters([
            'labels'
        ]),
        methods: {
            selectTarget(uid) {
                this.targetText = uid ? this.labels.find(e => e.uid === uid).text : '----';
                this.$store.commit('updateNode', {uid: this.node.uid, target: uid});
            },
            handleConfirm(text) {
                this.$store.commit('updateNode', {uid: this.node.uid, text: text});
            },
        },
        mounted() {
            this.targetText = this.node.target ? this.labels.find(e => e.uid === this.node.target).text : '----';
        }
    }
</script>

<style scoped>
    .option-node {
        width: 100%;
        padding-left: 4em;
        text-align: left;
        display: flex;
        flex-direction: row;
        align-items: center;
    }

    .text {
        flex: 6;
        text-align: left;
    }

    .icon {
        padding-right: .5em;
        padding-left: .5em;
    }

    .target {
        width: 12em;

        font-size: small;
        color: #909399;
        white-space: nowrap;
        text-overflow: ellipsis;
    }

    .target-button {
        width: 100%;
    }


</style>