<template>
    <Layout>
        <Header class="header">
            <span class="title">资源编辑器</span>
            <Button class="button" shape="circle" icon="ios-add" @click="createRes">添加空资源</Button>
            <Upload action="" :before-upload="createNewRes" multiple>
                <Button class="button" shape="circle" icon="ios-add-circle-outline">添加文件资源</Button>
            </Upload>
        </Header>
        <Content class="resources">
            <transition-group ref="resList" tag="div"
                              class="resource-list"
                              enter-active-class="animated fadeInRight fast"
                              leave-active-class="animated zoomOut fast"
            >
                <div class="resource-wrapper" v-for="(res, index) of resources" :key="res.uid">
                    <Resource class="resource" :resource="res" :index="index"/>
                </div>
            </transition-group>
        </Content>
    </Layout>
</template>

<script>
    import {Layout, Header, Content, Button, Upload} from 'view-design';
    import {mapMutations, mapState} from "vuex";
    import Resource from "@/components/resources/Resource";
    import {getFileUrl} from "@/util/misc";

    import 'animate.css';

    export default {
        name: "ResourceManager",
        components: {Resource, Layout, Header, Content, Button, Upload},
        computed: mapState(['resources']),
        methods: {
            createNewRes(file) {
                let url = getFileUrl(file);
                this.$store.commit('createRes', {name: file.name.split('.')[0], src: url});
                this.$nextTick(() => this.scrollToBottom());
                return false;
            },
            scrollToBottom() {
                let el = this.$refs.resList.$el;
                el.scrollTop = el.scrollHeight;
            },
            ...mapMutations(['createRes']),
        },
    }
</script>

<style scoped>
    .header {
        text-align: right;
        background: #f5f7f9 !important;
        border-bottom: .05em solid #c2c5cc;
        display: flex;
        flex-direction: row;
        align-items: baseline;
    }

    .header > .title {
        flex: 1;
        text-align: left;
        font-size: 1.5em;
        font-weight: bold;
    }

    .header > .button {
        margin-left: 1em;
    }

    .resources {
        width: 100%;
        padding: 1em;
        text-align: left;
        overflow-y: hidden;
    }

    .resource-list {
        width: 100%;
        height: 100%;
        overflow-x: hidden;
        overflow-y: auto;
    }

    .resource-wrapper {
        margin: 0;
        padding: 1em;
        display: inline-block;
    }

    .resource {
        margin: 0;
    }
</style>