<template>
    <Card class="resource" :bordered="false">
        <div class="card-header" slot="title">
            <BetterInput class="name" :init-value="resource.name" :buttons="false" @confirm="confirmName"/>
            <Upload class="icon" action="" :before-upload="uploadFile">
                <Icon :size="20" type="ios-open-outline"/>
            </Upload>
            <Icon class="icon" :size="20" type="ios-remove-circle-outline" @click="deleteRes"/>
        </div>
        <div v-if="resource.src" class="res-shortcut">
            <div class="shortcut-wrapper">
                <img :src="resource.src" :alt="resource.name"/>
            </div>
            <p class="text-src">{{ resource.src }}</p>
        </div>
        <Upload v-else class="no-res" action="" type="drag" :before-upload="uploadFile">
            <Icon size="52" type="ios-open-outline"/>
            <p>点击 或 拖动 以上传资源</p>
        </Upload>
    </Card>
</template>

<script>
    import {Card, Icon, Upload} from 'view-design';
    import BetterInput from "@/components/BetterInput";
    import {getFileUrl} from "@/util/misc";

    export default {
        name: "Resource",
        components: {BetterInput, Icon, Card, Upload},
        props: {
            resource: {
                type: Object,
                require: true,
            },
            index: {
                type: Number,
                require: true,
            },
        },
        methods: {
            confirmName(text) {
                this.$store.commit('updateRes', {uid: this.resource.uid, name: text});
            },
            deleteRes() {
                this.$store.commit('deleteRes', {index: this.index});
            },
            uploadFile(file) {
                let url = getFileUrl(file);
                this.$store.commit('updateRes', {uid: this.resource.uid, src: url});
                return false;
            }
        },
    }
</script>

<style scoped>
    .resource {
        width: 16em;
        height: 20em;
        display: flex;
        flex-direction: column;
    }

    .icon {
        padding: .2em;
    }

    .card-header {
        width: 100%;
        display: flex;
        flex-direction: row;
    }

    .name {
        flex: 1;
        flex-shrink: 0;
    }

    .card-header > .icon {
        flex-shrink: 0;
    }

    .no-res {
        width: 100%;
        height: 100%;
        margin-top: 1em;
        padding: 2em 0;
    }

    .res-shortcut {
        width: 100%;
        height: 12em;
        display: flex;
        flex-direction: column;
    }

    .shortcut-wrapper {
        width: 100%;
        flex: 1;
        text-align: center;
    }

    .shortcut-wrapper > img {
        width: auto;
        height: 8em;
        max-width: 100%;
        max-height: 100%;
    }

    .text-src {
        white-space: normal;
        word-break: break-all;
    }
</style>