<template>
    <div class="node">
        <!--角色选择-->
        <Dropdown class="role-select" trigger="click" @on-click="selectSpeaker">
            <Avatar :src="speakerSrc" icon="ios-contact"/>
            <DropdownMenu slot="list">
                <DropdownItem v-for="role of roles" :key="role.uid" :name="role.uid">{{ role.name }}</DropdownItem>
            </DropdownMenu>
        </Dropdown>
        <div class="info">
            <!--语者-->
            <span class="speaker">{{ speakerName }}</span>
            <!--内容编辑-->
            <BetterInput class="content-edit" :init-value="node.text" @confirm="handleConfirm"/>
        </div>
    </div>
</template>

<script>
    import {Dropdown, DropdownMenu, DropdownItem, Avatar} from 'view-design';
    import BetterInput from "@/components/BetterInput";

    import {mapState} from 'vuex';

    export default {
        name: "LineNode",
        components: {BetterInput, Dropdown, DropdownMenu, DropdownItem, Avatar},
        props: {
            node: {
                type: Object,
                required: true,
            },
        },
        data() {
            return {
                speakerSrc: '',
                speakerName: '',
            };
        },
        computed: {
            ...mapState([
                'roles',
                'resources',
            ]),
        },
        watch: {
            speakerText(newVal) {
                this.node.speaker = newVal;
            }
        },
        methods: {
            refreshSpeaker() {
                let role = this.roles.find(e => e.uid === this.node.role);
                if (role) {
                    this.speakerName = role.name;
                    if (role.avatar) {
                        let res = this.resources.find(e => e.uid === role.avatar);
                        if (res) {
                            this.speakerSrc = res.src;
                        }
                    } else {
                        this.speakerSrc = '';
                    }
                }
            },
            selectSpeaker(uid) {
                this.$store.commit('updateNode', {
                    uid: this.node.uid,
                    role: uid,
                    speaker: this.speakerText,
                });
                this.refreshSpeaker();
            },
            handleConfirm(text) {
                this.$store.commit('updateNode', {uid: this.node.uid, text});
            },
        },
        mounted() {
            this.refreshSpeaker();
        }
    }
</script>

<style scoped>
    .node {
        padding-left: 2em;
        display: flex;
        flex-direction: row;
        align-items: center;
    }

    .node > .role-select {
        margin-right: 1em;
        text-align: left;
    }

    .info {
        flex: 1;
        text-align: left;
    }

    .speaker {
        font-size: .5em;
        color: #909399;
    }

    .content-edit {
        width: 100%;
    }
</style>