<template>
    <div class="node" @mouseenter="hover = true" @mouseleave="hover = false">
        <component class="node-content" :is="component" :node="node"/>
        <div class="action-wrapper">
            <transition
                    enter-active-class="animated fadeInRight faster"
                    leave-active-class="animated fadeOutRight faster"
            >
                <div class="action" v-show="hover">
                    <Button type="warning" icon="ios-trash-outline" shape="circle" @click="deleteNode"/>
                    <div class="mover">
                        <Icon type="ios-arrow-up" :size="20" @click="move(-1)"/>
                        <Icon type="ios-arrow-down" :size="20" @click="move(1)"/>
                    </div>
                </div>
            </transition>
        </div>
    </div>
</template>

<script>
    import LineNode from "@/components/nodes/LineNode";
    import LabelNode from "@/components/nodes/LabelNode";
    import OptionNode from "@/components/nodes/OptionNode";

    import '../../../node_modules/animate.css/animate.css';

    import {Button, Icon} from 'view-design';

    export default {
        name: "Node",
        components: {LineNode, LabelNode, OptionNode, Button, Icon},
        props: {
            node: {
                type: Object,
                required: true,
            },
            index: {
                type: Number,
                required: true,
            },
            labels: {
                type: Array,
                required: false,
                default: () => [],
            },
        },
        data() {
            return {
                hover: false,
            };
        },
        computed: {
            component() {
                switch (this.node.type) {
                    case 'line': return LineNode;
                    case 'label': return LabelNode;
                    case 'option': return OptionNode;
                    default: return LineNode;
                }
            },
        },
        methods: {
            move(offset) {
                this.$store.commit('moveNode', {index: this.index, offset});
            },
            deleteNode() {
                this.$store.commit('deleteNode', {index: this.index});
            },
        }
    }
</script>

<style scoped>
    .node {
        width: 100%;
        display: flex;
        flex-direction: row;
        align-items: center;
        overflow: hidden;
    }

    .node-content {
        flex: 1;
        padding-top: .2em;
        padding-bottom: .2em;
    }

    .action-wrapper {
        width: 7em;
    }

    .action {
        margin-left: 1em;
        display: flex;
        flex-direction: row;
        align-items: center;
    }

    .mover {
        margin: 0 1em;
        display: flex;
        flex-direction: column;
    }
</style>