<template>
    <Layout>
        <Header class="header">
            <span class="title">角色编辑器</span>
            <Button class="button" icon="ios-add" shape="circle" @click="createRole">添加新角色</Button>
        </Header>
        <Content class="roles">
            <transition-group ref="resList" tag="div"
                              class="role-list"
                              enter-active-class="animated fadeInRight fast"
                              leave-active-class="animated zoomOut fast"
            >
                <div class="role-wrapper" v-for="(role, index) of roles" :key="role.uid">
                    <Role class="role" :role="role" :index="index"/>
                </div>
            </transition-group>
        </Content>
    </Layout>
</template>

<script>
    import {Layout, Header, Content, Button} from 'view-design';
    import {mapState} from "vuex";

    import '../../../node_modules/animate.css/animate.css';
    import Role from "@/components/roles/Role";

    export default {
        name: "RoleManager",
        components: {Role, Layout, Header, Content, Button},
        computed: mapState(['roles']),
        methods: {
            createRole() {
                this.$store.commit('createRole');
                this.$nextTick(() => this.scrollToEnd());
                return false;
            },
            scrollToEnd() {
                let el = this.$refs.resList.$el;
                el.scrollLeft = el.scrollWidth;
            },
        },
    }
</script>

<style scoped>
    .header {
        text-align: right;
        background: #f5f7f9 !important;
        border-bottom: .05em solid #c2c5cc;
        display: flex;
        flex-direction: row;
        align-items: baseline;
    }

    .header > .title {
        flex: 1;
        text-align: left;
        font-size: 1.5em;
        font-weight: bold;
    }

    .header > .button {
        margin-left: 1em;
    }

    .roles {
        width: 100%;
        padding: 1em;
        text-align: left;
        overflow-y: hidden;
    }

    .role-list {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: row;
        flex-shrink: 1;
        overflow-x: auto;
        overflow-y: hidden;
    }

    .role-wrapper {
        height: 100%;
        margin: 0;
        padding: 1em;
    }

    .role {
        width: 20em;
        height: 100%;
        margin: 0;
    }
</style>