<template>
    <div class="node-overview">
        <p class="title">大纲</p>
        <div class="nodes">
            <p v-for="node of nodes" :key="node.uid" :class="'node-preview ' + node.type">{{ getShortcut(node) }}</p>
        </div>
    </div>
</template>

<script>
    import {mapGetters} from 'vuex';

    export default {
        name: "NodeOverview",
        props: {
            nodes: {
                type: Array,
                required: true
            },
        },
        computed: mapGetters(['labels']),
        methods: {
            getShortcut(node) {
                switch (node.type) {
                    case 'line': return node.speaker ? `${node.speaker}: ${node.text}` : node.text;
                    case 'label': return node.text;
                    case 'option': return node.text + (node.target ? ' > ' + this.labels.find(e => e.uid === node.target).text : '');
                }
            },
        },
    }
</script>

<style scoped>

    .node-overview {
        width: 100%;
        height: 100%;
        padding: 0 0 0 1em;
        display: flex;
        flex-direction: column;
    }
    .title {
        margin-top: 1em;
        margin-bottom: 1em;
        font-size: 1.5em;
        font-weight: bold;
    }

    .nodes {
        width: 100%;
        padding-left: .5em;
        flex: 1;
        overflow-y: auto;
    }

    .node-preview {
        width: 100%;
        padding: 0;
        box-sizing: border-box;
        text-overflow: ellipsis;
        word-break: break-all;
        white-space: nowrap;
        overflow: hidden;
    }

    .label {
        font-size: 1.2em;
        padding-left: 0;
    }

    .line {
        font-size: 1em;
        padding-left: 1em;
    }

    .option {
        font-size: .8em;
        padding-left: 2em;
        color: #909399;
    }
</style>