<template>
    <Layout class="node-manager">
        <Sider class="sider" :width="250">
            <NodeOverview :nodes="nodes"/>
        </Sider>
        <Layout>
            <Header class="header">
                <p class="title">节点编辑器</p>
                <Button class="button" shape="circle" icon="ios-flag-outline" @click="addNode('label')">标签</Button>
                <Button class="button" shape="circle" icon="ios-git-commit" @click="addNode('line')">台词</Button>
                <Button class="button" shape="circle" icon="ios-git-branch" @click="addNode('option')">选项</Button>
            </Header>
            <Content class="nodes">
                <p v-if="!nodes.length" class="empty-node-list-hint">节点空空如也，点击右上方的按钮添加节点哦</p>
                <transition-group v-else tag="div" ref="nodeList" class="node-list"
                                  enter-active-class="animated fadeInUp fast"
                                  leave-active-class="animated fadeOutRight fast"
                >
                    <Node class="node-wrapper"
                          v-for="(node, index) of nodes"
                          :key="node.uid"
                          :node="node"
                          :index="index"
                    />
                </transition-group>
            </Content>
        </Layout>
    </Layout>
</template>

<script>
    import {Layout, Content, Sider, Header, Button} from "view-design";
    import Node from "@/components/nodes/Node";
    import NodeOverview from "@/components/nodes/NodeOverview";
    import {mapState} from 'vuex';

    import 'animate.css';

    export default {
        name: "NodeManager",
        components: {NodeOverview, Node, Layout, Content, Sider, Header, Button},
        computed: mapState([
            'nodes',
        ]),
        methods: {
            scrollToBottom() {
                let el = this.$refs.nodeList.$el;
                el.scrollTop = el.scrollHeight;
            },
            addNode(type) {
                this.$store.commit('createNode', {type});
                this.$nextTick(() => this.scrollToBottom());
            },
        },
    }
</script>

<style scoped>
    p, ul {
        margin: .2em 0;
        padding-left: 1em;
    }

    li {
        list-style: none;
    }

    .node-manager {
        height: 100%;
        margin: 0;
        padding: 0;
        border-bottom: .1em solid #909399;
        box-sizing: border-box;

        text-align: left;

        display: flex;
        flex-direction: column;
        overflow-y: hidden;
    }

    .nodes {
        width: 100%;
        height: 100%;
        margin: auto;
        padding: 1em 0;
        text-align: center;
        overflow: hidden;
    }

    .node-list {
        width: 100%;
        height: 100%;
        margin: auto;
        display: block;
        overflow-y: auto;
        overflow-x: hidden;
    }

    .node-wrapper {
        width: 80%;
        margin: auto;
        padding-top: .5em;
        padding-bottom: .5em;
        border-bottom: .05em solid #c2c5cc;
    }

    .sider {
        border-right: .01em solid #c2c5cc;
        background-color: #f5f7f9;
    }

    .empty-node-list-hint {
        margin-top: 2em;
        color: #909399;
    }

    .header {
        text-align: right;
        background: #f5f7f9 !important;
        border-bottom: .05em solid #c2c5cc;
        display: flex;
        flex-direction: row;
        align-items: baseline;
    }

    .header > .title {
        flex: 1;
        text-align: left;
        font-size: 1.5em;
        font-weight: bold;
    }

    .header > .button {
        margin-left: 1em;
    }
</style>