<template>
    <article class="welcome-text">
        <h1>申明</h1>
        <p>这是一个还未完成的作品，本喵不对任何特性负责哦~</p>
        <h1>食用方法</h1>
        <ol>
            <li>在资源管理器中，上传文件资源（但是资源无法持久保存，待解决），并给每个资源起一个易于识别的名字。</li>
