            <li>然后在管理器中按序添加你想要的剧情节点。</li>
        </ol>
        <p>节点有三种：</p>
        <ul>
            <li><strong>标签</strong> 这类节点，是一个在节点列表中的标记，标记一个位置，这个位置指向标签下一个节点。在编译后，这个节点会消失，指向它的指针会指向它的下一个台词节点。</li>
            <li><strong>台词</strong> 该类节点有两个主要关键点：讲述人 以及 文本。其中讲述人就是之前定义的角色。再编译后他会保留，并且添加至少一个选项，除非它是最后一个节点。</li>
            <li><strong>选项</strong> 它是上一个台词节点所包含的选项，它包含一个指向标签的指针。在编译后，呀会被添加到上一个台词节点所表示的台词内。</li>
        </ul>
