<template>
    <div class="better-input">
        <div v-if="editing" class="panel">
            <Input ref="ipt" class="value"
                   :size="size"
                   type="text"
                   placeholder="在此输入单行台词"
                   clearable
                   v-model="value"
                   @on-enter="confirmInput"
                   @on-blur="handleInputBlur"
            />
            <Button v-if="buttons" class="button" shape="circle" type="primary" icon="ios-checkmark" @click="confirmInput"/>
            <Button v-if="buttons" class="button" shape="circle" type="warning" icon="ios-close" @click="cancelInput"/>
        </div>
        <div v-else class="panel" >
            <span class="value" @click="editing = true">{{ value }}</span>
        </div>
    </div>
</template>

<script>
    import {Input, Button} from 'view-design';

    export default {
        name: "BetterInput",
        components: {Input, Button},
        props: {
            size: {
                type: String,
                required: false,
                default: () => 'default',
            },
            initValue: {
                type: String,
                required: false,
                default: () => '',
            },
            buttons: {
                type: Boolean,
                required: false,
                default: () => true,
            },
        },
        data() {
            return {
                prev: this.initValue || '非空内容',
                value: this.initValue || '非空内容',
                editing: false,
            };
        },
        watch: {
            initValue(newVal) {
                this.value = newVal || '非空内容';
                this.prev = newVal;
            },
            editing(newVal) {
                if (newVal) {
                    this.$nextTick(() => this.$refs.ipt.focus());
                }
            }
        },
        methods: {
            confirmInput() {
                this.editing = false;
                this.prev = this.value;
                this.$emit('confirm', this.value);
            },

            cancelInput() {
                this.editing = false;
                this.value = this.prev;
                this.$emit('cancel', this.value);
            },
            handleInputBlur() {
                if (!this.buttons) {
                    this.cancelInput();
                }
            }
        },
    }
</script>

<style scoped>
    .panel {
        width: 100%;
        display: flex;
        flex-direction: row;
        align-items: center;

        text-align: inherit;
    }

    .value {
        width: 0;
        line-height: 1.2em;
        flex: 1;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
    }

    .panel .button {
        margin-left: .5em;
    }

    span {
        padding: .5em 1em;
    }

</style>