<template>
  <Layout id="app">
    <Header class="app-header">
      <img class="logo" src="favicon.png" alt="logo"/>
      <h1 class="app-name">{{ name }}</h1>
      <Button class="button" icon="ios-home-outline" shape="circle" @click="page = 'home'">主页</Button>
      <Button class="button" icon="ios-menu-outline" shape="circle" @click="page = 'node-manager'">节点管理器</Button>
      <Button class="button" icon="ios-albums-outline" shape="circle" @click="page = 'resource-manager'">资源管理器</Button>
      <Button class="button" icon="ios-contact-outline" shape="circle" @click="page = 'role-manager'">角色管理器</Button>
      <Button class="button" icon="ios-open-outline" shape="circle" @click="load">载入</Button>
      <Button class="button" icon="ios-exit-outline" shape="circle" @click="save">保存</Button>
      <Button class="button" icon="ios-link-outline" shape="circle" @click="exportData">导出</Button>
      <Button class="button" icon="ios-link-outline" shape="circle" @click="inputting = true">导入</Button>
      <Button class="button" shape="circle" @click="test">测试</Button>
    </Header>
    <Content class="content">
      <Home v-if="page === 'home'"/>
      <NodeManager v-else-if="page === 'node-manager'"/>
      <ResourceManager v-else-if="page === 'resource-manager'"/>
      <RoleManager v-else-if="page === 'role-manager'"/>
    </Content>
    <Modal v-model="outputting"
           class="modal"
           title="复制以下文本并保存到本地"
           ok-text="复制"
           cancel-text="关闭"
           @on-ok="copyData"
           @on-cancel="outputting = false"
    >
      <p ref="outputData" class="modal-content">
        {{ outputData }}
      </p>
    </Modal>
    <Modal v-model="inputting"
           class="modal"
           title="在此处粘贴工程存档"
           ok-text="导入"
           cancel-text="取消"
           @on-ok="importData"
           @on-cancel="inputting = false"
    >
      <Input class="modal-input" v-model="inputData" type="textarea" ref="saveText" autosize/>
    </Modal>
  </Layout>
</template>

<script>

  import NodeManager from "@/components/nodes/NodeManager";
  import {Layout, Header, Content, Button, Modal, Input} from 'view-design';
  import {mapActions, mapState} from 'vuex';
  import ResourceManager from "@/components/resources/ResourceManager";
  import RoleManager from "@/components/roles/RoleManager";
  import Home from "@/components/Home";

  export default {
    name: 'app',
    components: {Home, RoleManager, ResourceManager, NodeManager, Layout, Header, Content, Button, Modal, Input},
    data() {
      return {
        page: 'home',
        outputting: false,
        inputting: false,
        outputData: null,
        inputData: null,
      }
    },
    computed: mapState(['name']),
    methods: {
      importData() {
        let data = JSON.parse(this.inputData);
        this.$store.commit('fillState', data);
        this.inputting = false;
      },
      exportData() {
        let json = JSON.stringify(this.$store.state);
        this.outputData = json;
        this.outputting = true;
      },
      copyData() {
        if (window.navigator.clipboard && window.navigator.clipboard.writeText) {
          window.navigator.clipboard.writeText(this.outputData);
        } else {
          this.$refs.outputData.select();
          window.document.execCommand('Copy');
        }
        // this.$Message.success('复制成功');
      },
      ...mapActions([
        'save',
        'load',
        'test',
      ]),
    },
  }
</script>

