<template>
    <div class="label">
        <BetterInput size="large" class="text" :init-value="node.text" @confirm="handleConfirm"/>
        <Icon :size="24" type="ios-image-outline"/>
        <!--背景选择-->
        <Dropdown trigger="click" @on-click="selectBackground">
            <Button class="bg-select">
                <span>{{ bgText }}</span>
                <Icon type="ios-arrow-down"/>
            </Button>
            <DropdownMenu slot="list">
                <DropdownItem name="">同上节点</DropdownItem>
                <DropdownItem v-for="(bg, index) of resources" :key="bg.uid" :name="bg.uid" :divided="!index">{{ bg.name }}</DropdownItem>
            </DropdownMenu>
        </Dropdown>
    </div>
</template>

<script>
    import {Dropdown, DropdownMenu, DropdownItem, Button, Icon} from 'view-design';
    import BetterInput from "@/components/BetterInput";
    import {mapState} from 'vuex';

    export default {
        name: "LabelNode",
        components: {BetterInput, Dropdown, DropdownMenu, DropdownItem, Button, Icon},
        props: {
            node: {
                type: Object,
                required: true,
            },
        },
        data() {
            return {
                bgText: '----',
            };
        },
        computed: mapState([
            'resources'
        ]),
        methods: {
            selectBackground(uid) {
                this.$store.commit('updateNode', {uid: this.node.uid, background: uid});
                this.bgText = uid ? this.resources.find(e => e.uid === uid).name : '----';
            },
            handleConfirm(text) {
                this.$store.commit('updateNode', {uid: this.node.uid, text});
            },
        },
        mounted() {
            this.bgText = this.node.background ? this.resources.find(e => e.uid === this.node.background).name : '----';
        }
    }
</script>

<style scoped>
    .label {
        display: flex;
        flex-direction: row;
        align-items: center;
    }

    .text {
        flex: 1;
        font-size: 1.2em;
        text-align: left;
    }

    .bg-select {
        width: 10em;
        margin-left: 1em;
    }
</style>