<template>
    <Card class="role">
        <div slot="title" class="card-header">
            <Avatar :src="avatarSrc"/>
            <BetterInput class="name" :buttons="false" :init-value="role.name" @confirm="confirmName"/>
            <Icon :size="20" type="ios-remove-circle-outline" @click="deleteRole"/>
        </div>
        <div ref="body" class="body">
            <div class="form-item">
                <span class="key">头像：</span>
                <Select class="value" v-model="avatarUid">
                    <Option v-for="res of resources" :key="res.uid" :value="res.uid">{{ res.name }}</Option>
                </Select>
            </div>
            <div class="form-item">
                <span class="key">立绘：</span>
                <Select class="value"  v-model="picUid">
                    <Option v-for="res of resources" :key="res.uid" :value="res.uid">{{ res.name }}</Option>
                </Select>
            </div>
            <div class="pic-wrapper">
                <img class="pic" v-if="role.pic" :src="picSrc" :alt="role.name"/>
            </div>
        </div>
    </Card>
</template>

<script>
    import {Card, Select, Option, Avatar, Icon} from 'view-design';
    import BetterInput from "@/components/BetterInput";
    import {mapState} from "vuex";
    export default {
        name: "Role",
        components: {BetterInput, Card, Select, Option, Avatar, Icon},
        props: {
            role: {
                type: Object,
                require: true,
            },
            index: {
                type: Number,
                required: true,
            },
        },
        data() {
            return {
                avatarUid: this.role.avatar,
                picUid: this.role.pic,
                avatarSrc: '',
                picSrc: '',
            }
        },
        watch: {
            avatarUid(uid) {
                this.updateData({avatar: uid});
                this.avatarSrc = uid ? this.resources.find(e => e.uid === uid).src : '';
            },
            picUid(uid) {
                this.updateData({pic: uid});
                this.picSrc = uid ? this.resources.find(e => e.uid === uid).src : '';
            },
        },
        computed: {
            ...mapState(["resources"]),
        },
        methods: {
            updateData(data) {
                this.$store.commit('updateRole', {uid: this.role.uid, ...data})
            },
            confirmName(text) {
                this.updateData({name: text});
            },
            deleteRole() {
                this.$store.commit('deleteRole', {index: this.index});
            },
        },
        mounted() {
            this.$refs.body.parentElement.style.height = '100%';
            this.avatarSrc = this.role.avatar ? this.resources.find(e => e.uid === this.role.avatar).src : '';
            this.picSrc = this.role.pic ? this.resources.find(e => e.uid === this.role.pic).src : '';
        }
    }
</script>

<style scoped>

    .role {
        display: inline-flex;
        flex-direction: column;
    }

    .card-header {
        width: 100%;
        display: flex;
        flex-direction: row;
        align-items: center;
    }

    .name {
        flex: 1;
        padding: 0 1em;
        text-align: center;
    }

    .form-item {
        width: 100%;
        margin-bottom: 1em;
        display: flex;
        flex-direction: row;
        align-items: center;
    }

    .key {
        padding-left: 1em;
    }

    .value {
        flex: 1;
    }

    .body {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
    }

    .pic-wrapper {
        width: 100%;
        height: 0;
        flex: 1;
        text-align: center;
    }

    .pic {
        height: 100%;
    }

</style>