import keys from '@/keys';
import Vuex from 'vuex';
import Vue from 'vue';
import createNode from "@/model/egg-node";
import createResource from "@/model/egg-resource";
import {getFileUrl} from "@/util/misc";
import createRole from "@/model/egg-role";
import testScript from "@/test/test-script";

Vue.use(Vuex);

const store = new Vuex.Store({
    state: {
        name: '皇帝的新剧本',
        nodes: [],
        roles: [],
        resources: [],
    },
    getters: {
        labels(state) {
            return state.nodes.filter(e => e.type === 'label');
        },
        lines(state) {
            return state.nodes.filter(e => e.type === 'line');
        },
    },
    mutations: {
        updateData(state, {arr, updatedData}) {
            let uid = updatedData.uid;
            let data = arr.find(e => e.uid === uid);
            if (data) {
                Object.assign(data, updatedData);
            }
        },
        // region nodes
        updateNode(state, updatedNode) {
            store.commit('updateData', {arr: state.nodes, updatedData: updatedNode});
        },
        createNode(state, {type}) {
            state.nodes.push(createNode(type));
        },
        deleteNode(state, {index}) {
            // let index = state.nodes.findIndex(e => e.uid === uid);
            if (index >= 0) {
                state.nodes.splice(index, 1);
            }
        },
        moveNode(state, {index, offset}) {
            // let index = state.nodes.findIndex(e => e.uid === uid);
            if ((offset === -1 && index > 0) || (offset === 1 && index < state.nodes.length - 1)) {
                let node = state.nodes[index];
                state.nodes.splice(index, 1);
                state.nodes.splice(index + offset, 0, node);
            }
        },
        // endregion
        // region resources
        updateRes(state, updatedRes) {
            store.commit('updateData', {arr: state.resources, updatedData: updatedRes});
        },
        createRes(state, data) {
            state.resources.push(createResource(data));
        },
        deleteRes(state, {index}) {
            if (index >= 0) {
                state.resources.splice(index, 1);
            }
        },
        // endregion
        // region roles
        updateRole(state, updatedRole) {
            store.commit('updateData', {arr: state.roles, updatedData: updatedRole});
        },
        createRole(state) {
            state.roles.push(createRole());
        },
        deleteRole(state, {index}) {
            if (index >= 0) {
                state.roles.splice(index, 1);
            }
        },
        // endregion
        fillState(state, newState) {
            Object.assign(state, newState);
        }
    },
    actions: {
        save({state}) {
            let json = JSON.stringify(state);
            localStorage.setItem(keys.LOCAL_STORAGE_KEY, json);
        },
        load({commit}) {
            let savedData = JSON.parse(localStorage.getItem(keys.LOCAL_STORAGE_KEY)) || {};
            commit('fillState', savedData);
        },
        test({commit}) {
            commit('fillState', testScript);
        },
        uploadNewFiles({commit}, files) {
            files.map(getFileUrl).forEach(url => commit('createRes', {src: url}));
        },
    },
});


export default store;