import GLOBALS from '@/data/globals';


/**
 * 生成一个UID
 * @returns {number} 生成的UID
 */
function nextUID() {
    return GLOBALS.uidg++;
}

function repeat(str, times) {
    return ''.concat(...Array(times).fill(str));
}

function fill(target, length, filler = '0') {
    let str = ('string' === typeof target) || String(target);
    let r = length - str.length;
    let rest = r > 0 ? repeat(filler, r) : '';
    return rest + str;
}

function getFileUrl(file) {
    let url = null;
    if (window.createObjcectURL) {
        url = window.createOjcectURL(file);
    } else if (window.URL) {
        url = window.URL.createObjectURL(file);
    } else if (window.webkitURL) {
        url = window.webkitURL.createObjectURL(file);
    }
    return url;
}

export {
    nextUID,
    repeat,
    fill,
    getFileUrl,
}