import {nextUID} from "@/util/misc";

function createNode(type) {
    let rest;
    switch (type) {
        case 'label': rest = {background: 0}; break;
        case 'line': rest = {role: 0}; break;
        case 'option': rest = {target: 0}; break;
        default: throw Error('cannot resolve type: ' + type);
    }
    return {
        type: type,
        uid: nextUID(),
        text: '',
        ...rest,
    };
}

export default createNode;